@charset "UTF-8";

/* _frame_mobile_sitemap.scss */

#sitemapToolbar {
  position:absolute;
  right:0;
  top:0;
  width:61px;
  height:48px;
  margin:0;
  border-left:1px solid #f0f0f0;
}
#sitemapToolbar .sitemapToolbarButton {
  width: 61px;
  height: 48px;
  border: 0;
}

#sitemapToolbar .sitemapToolbarButton:hover {
  border: 0;
  background-color : transparent;
}
#sitemapToolbar .sitemapToolbarButtonSelected {
  border: 0 !important;
  background-color : #4a7eec !important;
}


.sitemapTree{
  margin-left:0;
}
#sitemapSelectContainer {
  display:block
}
.sitemapPageLink{
  display:block;
  overflow:hidden;
  height:40px;
  margin:0 18px;
  border-bottom:1px solid #efefef;
  font-size:15px;
  line-height:40px;
  color:#4a4a4a;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.sitemapPageLink:hover {
  text-decoration: none;
}

.ico_sitemap_page {
  @include use-sprite-mobile-image(-162px);

  display: inline-block;
  width: 15px;
  height: 19px;
  margin-top:10px;
  vertical-align:top;
}
.sitemapPageName {
  margin-left:9px;
  font-size:15px;
  line-height:40px;
  color:#4a4a4a;
}

.sitemapNode{
  margin:0;
  white-space: nowrap;
}

.sitemapPageLinkContainer {
  padding:0;
}
.sitemapHighlight {
  background-color: #e1eeff;
  font-weight: normal;

  .ico_sitemap_page {
    @include use-sprite-mobile-image(-182px);
  }
  .sitemapPageName{
    font-weight:bold;
    color:#497eec;
  }
}
