@charset "UTF-8";

/* _frame_mobile.scss */

/* Mobile Viewer Frame Style */
html, body{
  position: relative;
  height: 100%;
}

.no_scrolling{
  overflow:hidden;
  height:100%;
}

input[type="submit"],
input[type="button"],
button {
  @include css3-prefix("appearance", "button");
  @include border-radius(0);
  @include gradient-vertical(#f7f7f7,#bbb);

  display:inline-block;
  padding:0 5px;
  margin:0;
  border:1px solid rgb(221,221,221);
}

.mobile{
  // 모바일 기기 해상도에서 zoom 속성으로 px-ratio 구현되어 있기 때문에 폰트가 작게 보여 질 수 있도록 text-size-adjust 속성 추가
  -webkit-text-size-adjust:auto;
  font-family:AppleSDGothicNeo-Regular,HelveticaNeue-Regular,sans-serif;
}

.blind{
  display: block;
  overflow: hidden;
  height: 100%;
  color: rgba(0,0,0,0);
}


/* Page Navigation */
.page_navi_area{
  position:fixed;
  right:14px;
  bottom:9px;
  width:120px;
  height:50px;
  font-size:8px;
  text-align:right;
}

.btn_viewer_menu {
  @include border-radius(50%);
  @include box-shadow(0, 3px, 0, rgba(178,178,178,.25));

  display:inline-block;
  position: relative;
  width:45px;
  height:45px;
  background:rgba(0,0,0,.3);
  border:0;

  &.on{
    background:rgba(0,0,0,.7);
  }
  &:first-child{
    margin-right:3px;
  }
}
.btn_viewer_menu.enabled{
  opacity : 1;
  pointer-event: visible;
}
.btn_viewer_menu.disabled{
  opacity : 0.2;
  pointer-event: none;
}

.btn_viewer_menu .ico {
  position: absolute;
  top: 50%;
  left: 50%;
  background-position: 0 0;
  content: "";

  &.prev {
    @include use-sprite-mobile-image(0);
    margin-top:-10px;
    margin-left:-7px;
    width: 12px;
    height: 20px;
  }
  &.next {
    @include use-sprite-mobile-image(-20px);
    margin-top:-10px;
    margin-left:-6px;
    width: 12px;
    height: 20px;
  }
  &.main_menu{
    @include use-sprite-mobile-image(-40px);

    width:24px;
    height:20px;
    margin-top:-10px;
    margin-left:-12px;

  }
  &.close{
    @include use-sprite-mobile-image(-60px);

    width:24px;
    height:20px;
    margin-top:-10px;
    margin-left:-12px;

  }
  &.page_note{
    @include use-sprite-mobile-image(-105px);

    width:25px;
    height:27px;
    margin-top:-13px;
    margin-left:-12px;
  }
  &.interaction{
    @include use-sprite-mobile-image(-133px);

    width:29px;
    height:29px;
    margin-top:-15px;
    margin-left:-15px;
  }
  &.sitemap{
    @include use-sprite-mobile-image(-80px);

    width:27px;
    height:25px;
    margin-top:-12px;
    margin-left:-14px;
  }
}

.loading_container{
  width:100%;
  height:100%;
  background-color:rgba(255,255,255,.7);
  position:absolute;
  z-index:10;
}

/* 메뉴 영역 */
.menu_panel{
  position:fixed;
  bottom:9px;
  left:14px;
  width:50px;
  height:50px;
  z-index:999990;
  zoom:1;
}

.sub_menu_area{
  position:fixed;
  bottom:65px;
  left:14px;

  .btn_viewer_menu{
    display:block;
    margin-bottom:7px;
  }
}

/* 페이지 노트 & 사이트맵 콘텐츠 */
.panel_content_container{
  position:fixed;
  bottom:0;
  left:0;
  right:0;
  height:60%;
  z-index:999999;
  background:#fff;
  font-size:12px;
  line-height:1.6;
  color:#868686;
  zoom:1;
}
.panel_content_area{
  overflow-x:hidden;
  overflow-y:auto;
  position: absolute;
  top:24px;
  left:0;
  right:0;
  bottom:0;
  -webkit-overflow-scrolling:touch;
}
.panel_content_title{
  padding-left:9px;
  font-size:9px;
  font-weight:normal;
  line-height:23px;
  background:#ececec;
}
.btn_panel_close{
  display:inline-block;
  position:absolute;
  top:-35px;
  left:50%;
  margin-left:-18px;
  width:35px;
  height:35px;
  background: transparent;
  border:0;

  &:after{
    @include use-sprite-mobile-image(-60px);

    display:inline-block;
    position:absolute;
    top:8px;
    left:8px;
    width:24px;
    height:20px;
    content:"";
  }
}

/* 페이지 노트 */
.note_panel{
  word-break: break-all;
}

.page_title{
  display:block;
  overflow:hidden;
  position:relative;
  height:39px;
  margin:0 18px;
  padding-left:23px;
  border-bottom:1px solid #efefef;
  font-size:15px;
  line-height:40px;
  color:#4a4a4a;

  &:before {
    @include use-sprite-mobile-image(-162px);

    display: inline-block;
    position: absolute;
    top: 0;
    left: 0;
    width: 15px;
    height: 19px;
    margin-top: 10px;
    content: "";
  }

  #currentPageTitle{
    @include ellipsis();
    font-weight:normal;
  }
}

.page_note_content{
  margin: 10px 18px;
  font-size:12px;
  line-height:21px;
  word-break: break-all;
}

.dim{
  position:fixed;
  top:0;
  left:0;
  right:0;
  bottom:0;
  z-index:999980;
  background:rgba(0,0,0,.7);
}
